nav {
  background-color: white;
  display: flex;
  position: fixed;
  z-index: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

nav a {
  text-align: center;
  margin: 25px;
  color: #596397;
  font-weight: 500;
}

nav a:link {
  text-decoration: none;
}

nav a:hover {
  color: #2c2c2c;
}
