.detail {
  width: 100%;
  height: 100%;
  position: fixed;
  text-align: center;
  top: 50px;
}
/* text-align: center; */
.detail__bg {
  position: absolute;
  width: 100%;
  z-index: -1;
  top: 0;
  left: 0;
}
.detail__section {
  display: flex;
  justify-content: center;
}
.detail__title {
  font-weight: 500;
  color: white;
}

.detail__description {
  font-weight: 300;
  color: white;
}

.detail__cover {
  width: 250px;
}
